.hero-container {
  background: url("https://i.postimg.cc/XJThr8Zh/img-home.jpg") center
    center/cover no-repeat;
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
  object-fit: contain;
}

.hero-text > h1 {
  color: #fff;
  font-size: 40px;
}
.hero-text {
  background-color: rgba(0, 0, 0, 0.3);
  text-align: center;
  padding: 10px 30px 30px 30px;
  border-radius: 20px;
}
.hero-text > h2 {
  color: lightcyan;
  font-size: 50px;
}

.hero-text > p {
  margin-top: 8px;
  color: #fff;
  font-size: 20px;
  max-width: 70vw;
  text-align: center;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
}

.hero-btns {
  margin-top: 32px;
}

.hero-btns .btn {
  margin: 6px;
}

.fa-play-circle {
  margin-left: 4px;
}

